// Empleados.js
import React, { useState,useEffect } from 'react';
import API from '../../utils/API';
import { Table, Form, Input, Space, Button, Typography, Divider } from 'antd';
const { Title } = Typography;

const Empleados = () => {
  const [empleados, setEmpleados] = useState(0)
  const [isLoading, setIsLoading] = useState(false)
  const [editable, setEditable]= useState(false)
  const [claveEmpleado, setClaveEmpleado]= useState(0)

  const [form] = Form.useForm()

  const titleStyle = {
    marginTop: 20
  }

  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  }
  
  const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
  }

  useEffect(() => {
    loadEmpleados()
    setIsLoading(true);
    API.getEmpleados()
      .then(({data}) => {
          data.forEach(e => e.nombreCompleto = `${e.nombre} ${e.apellido1} ${e.apellido2}`)
          setEmpleados(data)
        })
      .catch(console.log)
    if(claveEmpleado){
    API.getEmpleado(claveEmpleado)
          .then(({data}) => {
            form.setFieldsValue(data)
          })
          .catch(console.log)
    }else{
      form.setFieldsValue({
        _id: '',
        nombre: '',
        apellido1: '',
        apellido2: ''
      })
    }
    setIsLoading(false);
  },[editable,form,claveEmpleado])

  const columns = [
    {
      title : 'Nombre',
      dataIndex: 'nombreCompleto',
      key: 'nombreCompleto'
    },
    {
      title: 'Acci√≥n',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          <Button 
            type="primary"
            disabled={editable} 
            onClick={() => {
              setClaveEmpleado(record._id)
              setEditable(true)
            }}>
            Editar
          </Button>
          <Divider/>
          <Button 
            type="primary" 
            disabled={editable} 
            danger 
            onClick={() => deleteEmpleado(record._id)}>
              Borrar
          </Button>  
        </Space>
      ),
    },
  ]

  const loadEmpleados = () => {
    setIsLoading(true);
    API.getEmpleados()
      .then(({data}) => {
        data.forEach(e => e.nombreCompleto = `${e.nombre} ${e.apellido1} ${e.apellido2}`)
        setEmpleados(data)
      })
      .catch(console.log)
    setIsLoading(false);
  }

  const deleteEmpleado = (id) => {
    API.deleteEmpleado(id)
      .then(res => this.loadEmpleados())
      .catch(console.log)
  }

  const onFinish = (values) => {
    if(values._id){
        API.updateEmpleado(values)
          .then(setEditable(0))
          .catch(console.log)
    }else{
        values.fechaAlta = Date.now().toString()
        API.saveEmpleado(values)
          .then(setEditable(0))
          .catch(console.log)
    }
    console.log('Success:', values)
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo)
  }

  return (
    
    <div >
      {isLoading ? (
        <div>Loading ...</div>
      ) : (
        <div>
          <Title style={titleStyle} level={3}>Lista de empleados</Title>
          <Table 
            columns={columns} 
            dataSource={empleados} 
            footer={() => 
              <Button 
                disabled={editable}
                type="primary" 
                onClick={() => setEditable(true)}>
                Crear Empleado
              </Button>
            }
          />
          <Title style={titleStyle} level={4}>Empleado</Title>
          <Form {...layout} 
            form={form}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <Form.Item name="_id"/>
            <Form.Item
              label="Nombre"
              name="nombre"
              rules={[{required: true, message: 'Introduzca un nombre' }]}
            >
              <Input disabled={!editable}/>
            </Form.Item>
            <Form.Item
              label="Apellido 1"
              name="apellido1"
              rules={[{ required: true, message: 'Introduzca el primer apellido' }]}
            >
              <Input disabled={!editable}/>
            </Form.Item>
              <Form.Item
              label="Apellido 2"
              name="apellido2"
              rules={[{ required: true, message: 'Introduzca el segundo apellido' }]}
            >
              <Input disabled={!editable}/>
            </Form.Item>

            <Form.Item {...tailLayout}>
              <Space size="middle">
                <Button 
                  disabled={!editable}
                  type="primary" 
                  htmlType="submit" 
                  >
                  Guardar
                </Button>
                <Divider/>
                <Button 
                  disabled={!editable}
                  type="primary" danger
                  onClick={() => {
                    setClaveEmpleado(0)
                    setEditable(false)
                  }}
                  >
                  Volver
                </Button>	
              </Space>
            </Form.Item>
          </Form>
        </div>
      )}
    </div>
);
}

export default Empleados